<template>
  <div class="collection-wrapper">

    <h4 class="fancy collection-title"><span>{{ collection.title }}</span></h4>
    
    <div class="collection" v-if="collection.items && (collection.items.length > 0)">
      <items-grid :items="collection.items"></items-grid>
    </div>

  </div>


</template>

<script>
import ItemsGrid from './ItemsGrid.vue'

export default {
  name: 'Collection',

  props: {
    collection: Object
  }, 

  components: {
    ItemsGrid
  }
}
</script>

<style>

.collection {
  margin-bottom: 40px;
}

.collection-title {
  margin: 8px;
}

.fancy {
  line-height: 0.5;
  text-align: center;
}
.fancy span {
  display: inline-block;
  position: relative;  
}
.fancy span:before,
.fancy span:after {
  content: "";
  position: absolute;
  height: 5px;
  /*border-bottom: 1px solid #DDD;*/
  border-top: 1px solid #DDD;
  top: 2.5px;
  /*width: 600px;*/
  width: 50%;
}
.fancy span:before {
  right: 100%;
  margin-right: 15px;
}
.fancy span:after {
  left: 100%;
  margin-left: 15px;
}

</style>



