<template>
<div class="banners-view" v-if="banners && (banners.length > 0)" >
  <carousel 
      :indicators="true" 
      :auto="3000" 
      :responsive="67" 
      :loop="true">

    <carousel-item v-for="banner in banners">
      <img :src="banner.image_url">
    </carousel-item>

  </carousel>
</div>


</template>

<script>
import { Carousel, CarouselItem } from 'vue-m-carousel'

export default {
  name: 'Banners',

  props: {
    banners: Array
  }, 

  components: {
    Carousel,
    CarouselItem
  }
}
</script>

<style>

  /* carousel container styling */
  .banners-view {
    margin-bottom: 30px;
  }
  /* carousel img styling */
  .carousel-item img {
    width: 100%;
  }

  /* carousel dot styling */
  .banners-view .carousel-dot {
    width: 12px; height: 12px;
    border-radius: 6px;
    font-size: 0;
  }
  .banners-view .carousel-dot.active {
    background-color: #ccc;
  }

</style>



