<template>
  <article class="item">
    <a href="#/items/{{ item.key }}">
      
      <div class="full-width"><img :src="item.square_cover_photo_url" /></div>
      <section>
        <h5 class="item_title">{{ item.title }}</h5>
        <p style="margin: 0;"><span class="price">{{ item.price / 100 | currency "¥ " 2 }}</span></p>
      </section>
    </a>
  </article>
</template>

<script>
export default {
  name: 'Item',

  props: {
    item: Object
  }, 
}
</script>
<style>
  h5{
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  article.item {
    box-shadow: 0 1px 0.2em rgba(17, 17, 17, 0.2); 
    background-color: white;
  }
  
</style>



