<template>
  <div class="qty-form">
    <div class="minus-btn" @click="minus" :disabled="qty <= 0"> - </div>
    <input class="qty-input" :value="qty" />
    <div class="plus-btn" @click="plus"> + </div>
  </div>
</template>

<script>

export default {
  name: 'QtyInput',

  props: {
    qty: 1
  },

  methods: {
    plus () {
      this.qty = this.qty + 1
    },

    minus () {

      if (this.qty > 0) {
        this.qty = this.qty - 1
      }
    }
  }

}
</script>

<style>
.qty-form {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.qty-input {
  flex-grow: 1;
  text-align: center;
}
.plus-btn {
  width: 60px;
}
.minus-btn {
  width: 60px;
}
</style>


