<template>

<div id="itemSelect">

  <section>
    <div class="pull-left" style="margin-top: -20px; margin-right: 20px;">
      <img :src="item.square_cover_photo_url" style="width: 60px; height: 60px;"/>
    </div>

    <div class="pull-left">
      <div class="clearfix">
        <h5 class="price">{{ item.price / 100 | currency "¥ " 2 }}</h5>
        <p>请选择</p>
      </div>
    </div>

    <div class="pull-right">
      <div class="mute small button pseudo" v-on:click.self="dismiss">[x]</div>
    </div>

    <div class="clearfix"></div>
  </section>

  <hr/>

  <section v-if="item.sizes && (item.sizes.length > 0) ">
    <article class="flex five">
      <div class="fifth">尺寸</div>

      <div class="four-fifth">
        <div v-for="size in item.sizes" 
          class="size small skew button" 
          :class="{'outline': (size != pickedSize)}"
          v-on:click.self="pickSize(size)">

          {{ size.label }}
        </div>
      </div>
    </article>
    <hr class="cell" />
  </section>


  <section v-if="item.colors && (item.colors.length > 0)">
    <article class="flex five">
      <div class="fifth">颜色</div>

      <div class="four-fifth">
        <div v-for="color in item.colors" 
          class="color small skew button outline" 
          :class="{'outline': (color != pickedColor)}"
          v-on:click.self="pickColor(color)">

          {{ color.label }}
        </div>
      </div>
    </article>
    <hr class="cell" />
  </section>

  <section>
    <article class="flex five">
      <div class="fifth">数量</div>
      <div class="four-fifth">
        <qty-input :qty="1"></qty-input>
      </div>
    </article>
    
  </section>

  <section style="position: fixed; bottom: 10px; height: 44px;">
    <button class="button outline success" 
      style="width: 100%;"
      :disabled="isDisabled()"
      @click="addToCart">
        加入购物车
    </button>
  </section>
</div>
</template>

<script>

import QtyInput from './QtyInput.vue'
import MessageBox from 'vue-msgbox'

export default {
  name: 'ItemSelectView',

  props: {
    item: Object,

    pickedSize: Object,

    pickedColor: Object, 
  },

  data() {
    return {
      qty: 1
    }
  }, 

  components: {
    QtyInput
  }, 

  methods: {

    pickSize(size) {
      console.log("[ItemSelectView pickSize] size: " + size.label)
      this.$set("pickedSize", size)
    },

    pickColor(color) {
      console.log("[ItemSelectView pickColor] color: " + color.label)
      this.$set("pickedColor", color)
    },

    validateSize() {
      var sizeValid = false
      // check if sizes picked
      if (this.item.sizes && this.item.sizes.length > 0) {
        sizeValid = (this.pickedSize ? true : false)
      } else {
        // no sizes; pass validation
        sizeValid = true
      }

      return sizeValid
    }, 

    validateColor() {
      var colorValid = false
      // check if colors picked
      if (this.item.colors && this.item.colors.length > 0) {
        colorValid = (this.pickedColor ? true : false)
      } else {
        // no colors; pass validation
        colorValid = true
      }

      return colorValid
    }, 

    isDisabled() {
      return !(this.validateColor() && this.validateSize())
    },

    isUserLoggedIn() {
      if (typeof APP_SHOP_USER == 'undefined') {
        return false
      }
      
      return (APP_SHOP_USER && (APP_SHOP_USER != "TEMPLATE_APP_SHOP_USER"))
    },

    addToCart () {

      if (!this.isUserLoggedIn()) {

        // callback to native and return 
        didAddToCart({})

        return 
      }

      // payload
      var cartItem = {
        item_key : this.item.key,
        qty: this.qty
      }

      // any size option?
      if (this.pickedSize) {
        // cartItem.size = this.pickedSize
        cartItem.size = this.pickedSize.label
      }

      // any color option?
      if (this.pickedColor) {
        // cartItem.color = this.pickedColor
        cartItem.color = this.pickedColor.label
      }

      console.log("[ItemSelectView addToCart] " + JSON.stringify(cartItem))

      var vm = this;

      // request callback
      var callback = function(response) {
          console.log("[ItemSelectView addToCart callback] response: " + JSON.stringify(response.body))

          var itemData = JSON.stringify(response.body)
          didAddToCart(itemData)

          MessageBox("成功", "加入了购物车", {
            type: "success"
          }).then(function(value, action) {
            vm.dismiss()
          })
      }

      var errorCallback = function(response) {
          console.log("[ItemSelectView addToCart error] response: " + JSON.stringify(response))

          // TODO: callback to native     

          MessageBox("失败", "抱歉发生了错误，请稍候再试", {
            type: "error"
          }).then(function(value, action) {
            vm.dismiss()
          })
      }

      // request url 
      var url = API_URL + "/api/cart/add"

      // request headers
      var headers = {
          "Content-Type": "application/json",
          "Accept": "application/json",
          "X-APP-KEY": APP_KEY,
          "X-APP-SECRET": APP_SECRET,
          "X-APP-SHOP-USER": APP_SHOP_USER
      }

      // vue-resource
      var params = {"add": cartItem }
      this.$http.patch(url, params, {headers: headers}).then(callback, errorCallback)
    }, 

    dismiss() {
      // reset pickSize and pickColor
      this.pickedSize = null
      this.pickedColor = null
      this.$parent.dismissItemSelect()
    }
  }

}
</script>


<style>
  #itemSelect {
    height: 60%;
    margin-top: 100%;
    width: 100%;
    background-color: white;
    position: fixed;
    z-index: 400; /* more than 300 */
  }
  .skew.button {
    min-width: 44px;
    margin: 0 10px;
  }

  .msgbox-header {
    text-align: center;
  }

  button.msgbox-btn {
    color: #0038D9;
  }
</style>



