<template>

<div id="item-view">

  <dark-cover v-show="showSelect" transition="fade" v-on:click.self="dismissItemSelect">
  </dark-cover>

  <nav-bar></nav-bar>

  <empty-item-view v-show="showEmpty"></empty-item-view>

  <loading-cover v-show="isLoading" transition="fade">
  </loading-cover>

  <div id="item-view-container">

    <item-select v-show="showSelect" :item="item" transition="">
    </item-select>

    <div class="item-view">

      <carousel :indicators="true" :auto="3000" :responsive="100" :loop="true">
        <carousel-item v-for="photo in item.photos">
          <img :src="photo.image_url">
        </carousel-item>
      </carousel>

    </div>

    <item-view-body :item="item"></item-view-body>

    <div style="height: 70px; width: 100%;"><!-- pad footer cta --></div>

    <div class="cta-footer center">      
      <div class="button outline success" style="width: 100%;" v-on:click.self="addToCart">加入购物车</div>
    </div>

  </div>
</div>

</template>

<script>
import { Carousel, CarouselItem } from 'vue-m-carousel'

import NavBar from '../shared/NavBar.vue'
import LoadingCover from '../shared/LoadingCover.vue'
import DarkCover from '../shared/DarkCover.vue'
import EmptyItemView from '../shared/EmptyItemView.vue'

import ItemSelect from './ItemSelect.vue'
import ItemViewBody from './ItemViewBody.vue'

import Vue from 'vue'

// custom transition
Vue.transition('slideUp', {
  enterClass: 'slideUp-enter',
  leaveClass: 'slideDown-leave'
})

export default {
  name: 'ItemView',

  data () {
    return {
      item: {},
      isLoading: true,
      showSelect: false,
      showEmpty: false
    }
  }, 

  components: {
    Carousel,
    CarouselItem, 
    EmptyItemView,
    LoadingCover,
    DarkCover,
    ItemViewBody,
    ItemSelect,
    NavBar
  },

  ready() {

  }, 

  route: {
    data ({ to }) {
      // This is the route data hook. It gets called every time the route
      // changes while this component is active.
      // 
      // What we are doing:
      // 
      // 1. Get the `to` route using ES2015 argument destructuring;
      // 2. Get the `page` param and cast it to a Number;
      // 3. Fetch the items from the store, which returns a Promise containing
      //    the fetched items;
      // 4. Chain the Promise and return the final data for the component.
      //    Note we are waiting until the items are resolved before resolving
      //    the entire object, because we don't want to update the page before
      //    the items are fetched.
      var itemId = to.params.id
      document.title = 'Lion IQ Item Detail View'

      // callback to native
      didItemDetail()

      // fetch item from API
      this.getItem(itemId)
      console.log("[ItemView route] params: " + itemId)
    },

    deactivate (transition) {
      // reset state
      this.$set('isLoading', true)
      this.$set('showSelect', false)
      this.$set('showEmpty', false)
      transition.next()
    }
  },

  methods: {

    addToCart() {
      this.$set('showSelect', true)
    }, 

    dismissItemSelect() {
      this.$set('showSelect', false)
    },

    getItem (itemId) {
      var vm = this;

      // request callback
      var callback = function(response) {
          console.log("[ItemView callback] response: " + JSON.stringify(response.body))
          
          // bind item model
          var item = response.json()
          vm.$set('item', item)

          // remove loading cover
          vm.$set('isLoading', false)

          // calback to native
          didLoadItemDetail(item)

          // change document title too
          document.title = this.item.title + ' | Lion IQ'
      }

      var errorCallback = function(response) {
          console.log("[error callback] response: " + JSON.stringify(response))
          
          // remove loading cover
          vm.$set('isLoading', false)

          // show error
          vm.$set('showEmpty', true)
      }

      // request url 
      var url = API_URL + "/api/items/" + itemId;

      // request headers
      var headers = {
          "Content-Type": "application/json",
          "Accept": "application/json",
          "X-APP-KEY": APP_KEY,
          "X-APP-SECRET": APP_SECRET
      };

      // vue-resource
      this.$http.get(url, {headers: headers}).then(callback, errorCallback);
    },

  }

}
</script>


<style>

  /* carousel img styling */
  .carousel-item img {
    width: 100%;
  }

  /* carousel dot styling */
  .item-view .carousel-dot {
    width: 12px; height: 12px;
    border-radius: 6px;
    font-size: 0;
  }
  .item-view .carousel-dot.active {
    background-color: #ccc;
  }
    
  /* Call-To-Action footer buttons*/
  .cta-footer {
    width: 100%;
    border-top: 1px solid #ddd;
    background-color: white;
    padding: 8px 16px 8px 16px;
    position: fixed;
    bottom: 0;
    z-index: 6; /* underneath cover views */
  }

  .cta-footer > section {
    text-align: center;
  }
</style>


