<template>
  <div id="shop">
    <!-- navbar -->
    <nav-bar-placeholder></nav-bar-placeholder>

    <!-- full screen covers -->
    <error-view v-show="showError"></error-view>

    <loading-cover v-show="isLoading" transition="fade">
    </loading-cover>


    <!-- shop main content -->
    <banners :banners="banners"></banners>

    <collection v-for="collection in collections" :collection="collection">
    </collection>


    <div id="shop-footer">
      <!-- tab bar -->
      <tab-bar-placeholder></tab-bar-placeholder>
    </div>

  </div>
</template>

<script>
import NavBarPlaceholder from './shared/NavBarPlaceholder.vue'
import TabBarPlaceholder from './shared/TabBarPlaceholder.vue'

import LoadingCover from './shared/LoadingCover.vue'
import ErrorView from './shared/ErrorView.vue'

import Banners from './banner/Banners.vue'
import Collection from './item/Collection.vue'

export default {
  name: 'ShopView',

  components: {
    NavBarPlaceholder,
    TabBarPlaceholder,

    LoadingCover,
    ErrorView, 

    Collection, 
    Banners,
  }, 

  ready() {
    this.loadFromShopData()
    this.getCollections()
    this.getBanners()
  }, 

  data() {
    return {
      collections: shopData["collections"],
      banners: shopData["banners"],
      isLoading: true,
      showError: false
    }
  }, 

  route: {
    data ({ to }) {
      // callback to native
      didMain()
    }
  },

  methods: {

    loadFromShopData() {

      this.$set('collections', shopData["collections"])

      this.$set('banners', shopData["banners"])

      // remove loading cover
      this.$set('isLoading', false)
    },

    getCollections () {
      var vm = this;

      // request callback
      var callback = function(response) {
          console.log("[getCollections callback] response: " + JSON.stringify(response.body));
          vm.$set('collections', response.json());

          // remove loading cover
          vm.$set('isLoading', false)
      }

      var errorCallback = function(response) {
          console.log("[error callback] response: " + JSON.stringify(response));

          // remove loading cover
          vm.$set('isLoading', false)

          // show error
          vm.$set('showError', true)
      }

      // request url 
      var url = API_URL + "/api/collections";

      // request headers
      var headers = {
          "Content-Type": "application/json",
          "Accept": "application/json",
          "X-APP-KEY": APP_KEY,
          "X-APP-SECRET": APP_SECRET
      };

      // vue-resource
      this.$http.get(url, {headers: headers}).then(callback, errorCallback);
    }, 

    getBanners () {
      var vm = this;

      // request callback
      var callback = function(response) {
          console.log("[getBanners callback] response: " + JSON.stringify(response.body));
          vm.$set('banners', response.json());
      }

      var errorCallback = function(response) {
          console.log("[getBanners error callback] response: " + JSON.stringify(response));
      }

      // request url 
      var url = API_URL + "/api/banners";

      // request headers
      var headers = {
          "Content-Type": "application/json",
          "Accept": "application/json",
          "X-APP-KEY": APP_KEY,
          "X-APP-SECRET": APP_SECRET
      };

      // vue-resource
      this.$http.get(url, {headers: headers}).then(callback, errorCallback);
    }
  }
}
</script>

<style>
/* shop content below navbar */ 
  #shop {
    margin-top: 64px;
    padding-top: 0px;
    padding-bottom: 49px;
    /*gray background simulate tableview*/
    background-color: #F6F6F6;
  }

  #shop-footer {
    position: fixed;
    bottom: 0;
    z-index: 6; /* underneath cover views */
    width: 100%;
  }
</style>


